import { USER_API } from "@/api/client";
import { mapState } from "vuex";

export default {
  data() {
    return {
      historyPhaseList: [],
      isLoading: true
    };
  },
  computed: {
    ...mapState(["local"]),
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
  },
  beforeMount() {
    this.isBitKeep && BitKeepInvoke.onLoadReady(() => {
      BitKeepInvoke.setTitle(this.$t("mining.historyTitle"));
      BitKeepInvoke.setIconAction('');
      BitKeepInvoke.appMode((err, res) => {
        let body = document.getElementsByTagName("body")[0];
        if (res == 1) {
          this.theme = 1;
          body.setAttribute("class", "theme-dark");
        } else {
          this.theme = 0;
          body.setAttribute("class", "theme-light");
        }
      });
    });
  },
  // beforeMount() {
    // BitKeepInvoke.setTitle(this.$t("mining.historyTitle"));
    //     BitKeepInvoke.setIconAction();
    // window.addEventListener("load", () => {
    //     BitKeepInvoke.onLoadReady(() => {

    //     //设置主题
    //       this.$nextTick(() => {
    //         BitKeepInvoke.appMode((err, res) => {
    //           let body = document.getElementsByTagName("body")[0];
    //           if (res == 1) {
    //             this.theme = 1;
    //             body.setAttribute("class", "theme-dark");
    //           } else {
    //             this.theme = 0;
    //             body.setAttribute("class", "theme-light");
    //           }
    //         });
    //     })
    //   });
    // });
  // },
  mounted() {
    this.historyPhase();
  },
  methods: {
    async historyPhase() {
      const { data, status } = await USER_API.historyPhase();
      if (status == 1) {
        this.isLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("CbkbExchange.know"),
          confirmButtonColor: "#495BFF",
        });
      }
      this.historyPhaseList = data;
      this.isLoading = false;
    },
  },
};
